.popup__container {
  max-width: 430px;
  min-height: 330px;
  border-radius: 10px;
  background-color: #fff;
  position: relative;
  padding: 34px 36px 36px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 768px) {
  .popup__container {
    max-width: 240px;
    min-height: 258px;
    padding: 30px 20px;
  }
}
