.button-editor {
  border: 1px solid #FFFFFF;
  background-image: url(../../images/button-editor/edit.svg);
  height: 24px;
  width: 24px;
  padding: 0;
  background-color: transparent;
  margin-top: 20px;
  opacity: 1;
  transition: opacity 0.2s linear;
  outline: none;
  background-position: center;
  background-repeat: no-repeat;
}

.button-editor:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .button-editor {
    width: 18px;
    height: 18px;
    background-size: cover;
    background-size: 7.5px;
    margin-left: 6px;
    
  }
}
