@import url(./__counter/button-like__counter.css);

.button-like {
  background-image: url(../../images/button-like/like-button.svg);
  border: none;
  width: 21px;
  height: 18px;
  background-color: transparent;
  padding: 0;
  opacity: 1;
  transition: opacity 0.2s linear;
}

.button-like:hover {
  opacity: 0.6;
  cursor: pointer;
}
