.popup__field {
  min-height: 27px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-size: 14px;
  line-height: 1.2;
  font-weight: 400;
  padding-bottom: 8px;
  outline: none;
}

@media screen and (max-width: 768px) {
  .popup__field {
    font-size: 12px;
    line-height: 1;
    padding-bottom: 0;
    min-height: 15px;
    padding-bottom: 2px;
  }
}
