.profile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  padding: 40px 0 50px 0;
}

@media screen and (max-width: 768px) {
  .profile{
    flex-direction: column;
    padding: 42px 0 36px;
  }
}
