.button-delete {
  background-image: url(../../images/button-delete/button-delete.svg);
  background-color: transparent;
  position: absolute;
  top: 18px;
  right: 15px;
  width: 18px;
  height: 19.3px;
  object-fit: cover;
  border: none;
  opacity: 1;
  transition: opacity 0.2s linear;
  z-index: 1;
}

.button-delete:hover {
  opacity: 0.6;
  cursor: pointer;
}
