.button-add {
  border: 2px solid #fff;
  border-radius: 2px;
  outline: none;
  background-color: transparent;
  background-image: url(../../images/button-add/add-button.svg);
  background-position: center;
  background-repeat: no-repeat;
  width: 150px;
  height: 50px;
  opacity: 1;
  transition: opacity 0.2s linear;
  padding: 0;
}

.button-add:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .button-add {
    width: 100%;
    background-size: 12px;
  }
}
