.profile__avatar-overlay {
  background: rgba(0, 0, 0, 0.8) url(../../../images/profile/pen.png) no-repeat center center;
  width: 120px;
  height: 120px;
  border-radius: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.1s linear;
}

.profile__avatar-overlay:hover {
  opacity: 1;
}
