.popup__close {
  background-image: url(../../../images/popup/close.svg);
  background-position: center;
  position: absolute;
  top: -40px;
  right: -40px;
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  padding: 0;
  opacity: 1;
  transition: opacity 0.2s linear;
  outline: none;
}

.popup__close:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .popup__close {
    width: 30px;
    height: 32px;
    top: -32px;
    right: -30px; 
    object-fit: cover;
    background-image: url(../../../images/popup/close2.svg);
  }
}
