.popup__save {
  min-width: 358px;
  min-height: 50.15px;
  background-color: #000;
  border-radius: 2px;
  border: none;
  color: #fff;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
  padding: 0;
  opacity: 1;
  transition: opacity 0.2s linear;
  outline: none;
  margin-top: 18px;
}

.popup__save:hover {
  opacity: 0.8;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .popup__save {
    min-width: 200px;
    font-size: 14px;
    margin-top: 16px;
  }
}
